.donut-charts-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: left;
  gap: 20px;
}

.chart-wrapper {
  flex: 1 1 100%;
  max-width: 100%;
  margin-bottom: 20px;
}

.chart-title {
  text-align: left;
  font-size: 1.2em;
  margin-bottom: 10px;
  margin-left: 20px;
}

.chart-container {
  height: 400px; /* Increased height */
  width: 100%;
  max-width: 400px; /* Added max-width */
}

.legend-container {
  margin-top: 10px;
  margin-left: 20px;
  max-width: 400px;
}

.legend-table {
  width: 90%;
  font-size: 1em;
  border-collapse: collapse;
}

.legend-table th,
.legend-table td {
  padding: 5px;
}

.legend-table th {
  font-weight: bold;
}

.legend-table-key-col {
  width: 15px;
}

.text-left {
  text-align: left;  
}

.text-right {
  text-align: right;  
}

.color-key {
  display: inline-block;
  width: 14px;
  height: 14px;
  border-radius: 50%;
  margin-right: 5px;
}

@media (min-width: 768px) {
  .chart-wrapper {
    flex: 1 1 calc(50% - 20px);
    max-width: calc(50% - 20px);
  }
}

@media (min-width: 1200px) {
  .chart-wrapper {
    flex: 1 1 calc(33.333% - 20px);
    max-width: calc(33.333% - 20px);
  }
}